:root {
    --primary-color: #536DFE;
    --primary-background: linear-gradient(#536DFE, #a553fe);
    /* teal-a200 */
    --success-color: #64FFDA;
    /* red-a400 */
    --error-color: #FF1744;
    /* amber a400 */
    --warning-color: #FFC400;
    --secondary-text-color: rgba(0, 0, 0, 0.7);
    --secondary-cta-color: rgba(255, 255, 255, 0.7);
    --box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
    --max-width: 368px;
}

html,
body {
    overflow-x: hidden;
    min-height: 100%;
    display: flex;
    flex-grow: 1;
    margin: 0;
    background: var(--primary-background);
    color: white;
}

body {
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
    display: flex !important;
    will-change: unset !important;
    transform: none !important;
    animation: none !important;
    animation-fill-mode: none !important;
}

.x-screen,
.x-screen-fit {
    will-change: transform;
    transform-origin: center;
    position: absolute;
    display: none;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
    width: 100%;
    height: 100%;
    animation-fill-mode: both;
    animation-duration: 600ms;
}

.x-screen {
    padding: 24px 16px;
    box-sizing: border-box;
}

.x-entry-animation {
    animation-name: from-right;
}

.x-exit-animation-reverse {
    animation-name: from-left;
}

.x-exit-animation {
    animation-name: from-left;
}

.x-entry-animation-reverse {
    animation-name: from-right;
}

.x-exit-animation,
.x-entry-animation-reverse {
    animation-direction: reverse;
    animation-duration: 300ms;
}

x-slides{
    flex-grow: 1;
    width: 100%;
    position: relative;
    /*height: 100%;*/
